.main-navigation {
  display: block;
  width: 100%;
  ul {
    display: none;
    list-style: none;
    margin: 0;
    padding-left: 0;
    ul {
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      float: left;
      position: absolute;
      top: 100%;
      left: -999em;
      z-index: 99999;
      ul {
        left: -999em;
        top: 0;
      }
      li {
        &:hover {
          >ul {
            display: block;
            left: auto;
          }
        }
      }
      li.focus {
        >ul {
          display: block;
          left: auto;
        }
      }
      a {
        width: 200px;
      }
    }
    li {
      &:hover {
        >ul {
          left: auto;
        }
      }
    }
    li.focus {
      >ul {
        left: auto;
      }
    }
  }
  li {
    position: relative;
  }
  a {
    display: block;
    text-decoration: none;
  }
}
.menu-toggle {
  display: block;
}
.main-navigation.toggled {
  ul {
    display: block;
  }
}
.site-main {
  .comment-navigation {
    margin: 0 0 1.5em;
  }
  .posts-navigation {
    margin: 0 0 1.5em;
  }
  .post-navigation {
    margin: 0 0 1.5em;
  }
}
.comment-navigation {
  .nav-links {
    display: flex;
  }
  .nav-previous {
    flex: 1 0 50%;
  }
  .nav-next {
    text-align: end;
    flex: 1 0 50%;
  }
}
.posts-navigation {
  .nav-links {
    display: flex;
  }
  .nav-previous {
    flex: 1 0 50%;
  }
  .nav-next {
    text-align: end;
    flex: 1 0 50%;
  }
}
.post-navigation {
  .nav-links {
    display: flex;
  }
  .nav-previous {
    flex: 1 0 50%;
  }
  .nav-next {
    text-align: end;
    flex: 1 0 50%;
  }
}
